<template>
  <div>
      <div class="shadow p-3 mb-5 bg-white rounded">
        <p class="text-info">Bir süredir Vue js ile ilgili sadece kodlayarak video çekip yüklüyorum. Buradan onlara ulaşabilirsiniz.</p>  
          </div>

    <div class="d-inline"  :key="index" v-for="(item,index) in items.items">
    
        <div  class="card  text-white bg-info mb-3" style="width: 320px;margin:0px 10px;padding-right:15px;float:left">
          <iframe
            width="320"
            height="400"
            :src="getUrl(item.snippet.resourceId.videoId)"
            frameborder="0"
            allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
            allowfullscreen
          ></iframe>
          <div class="card-body">
            <h5 class="card-title">{{item.snippet.title}}</h5>
            <p class="card-text">{{item.snippet.description}}</p>            
          </div>
        </div>
     
    </div>
  </div>
</template>

<script>
export default {
  name: "MyVideo",
  data() {
    return {
      items: []
    };
  },
  created() {
    fetch(
      "https://www.googleapis.com/youtube/v3/playlistItems?part=snippet%2CcontentDetails&maxResults=50&playlistId=PLDG4qXSgXzW5vFHkq6H2dJeTgHGxh8tVd&key=AIzaSyDqg9vDShgSwqtmDKeEaYcpSlCUTx2VWrg"
    )
      .then(res => {
        return res.json();
      })
      .then(videos => {
        this.items = videos;
      });
  },

  methods:{
      getUrl(videoid)
      {
          var youtubeEmbed='https://www.youtube.com/embed/';
          return youtubeEmbed+videoid;
      }
  }
};
</script>

<style scoped>

ul#menu li {
  display:inline;
}
</style>
